.tui-pagination {
  text-align: center;
  margin-bottom:30px;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  margin-right: 1px;
  width: 40px;
  padding: 12px 0 12px;
  color: black;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination .tui-page-btn:hover {
  opacity: 0.8;
  background-color: #ff6b08;
  color: white;
  border-radius: 5px;
  transition: background 250ms ease-in-out 0s;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  background-color: #ff6b08;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #ff6b08;
  opacity: 1;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  margin: 0;
  overflow: hidden;
  height: 40px;
  padding: 0;
  font-size: 0;
  line-height: 35px;
  white-space: nowrap;
  font-weight: normal;
  background-color: #f7f7f7;
  border-radius: 5px;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;

  border-radius: 5px;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
  margin-left: 2px;
  background-color: #f7f7f7;
  border-radius: 5px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  margin-right: 2px;
  background-color: #f7f7f7;
  border-radius: 5px;
}

.tui-pagination .tui-ico-first {
  margin-top: 5px;
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  margin-top: 5px;
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  margin-top: 5px;
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  margin-top: 5px;
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 20px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled {
  display: none;
  background: #f7f7f7;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  display: none;
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  display: none;
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  display: none;
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  display: none;
  background-position: -13px -12px;
}

.tui-page-inputs .tui-page-current {
  color: #ff6b08;
}
.tui-page-btn {
  background-color: #fff;
}
